import React, { useState } from 'react'
import InlineIconLabel from '../InlineIconLabel'
import OutlineButton from './OutlineButton'
import PrimaryButton from './PrimaryButton'
import { Purple } from '../colors'
import Modal from '../Modal'

function ArchiveButton(params) {
  const [open, setOpen] = useState(false)

  return (
    <span>
      {open && <Modal
        closeOnClick={() => setOpen(false)}
        textAlign='left'
        buttonAlign='right'
        buttons={[
          <OutlineButton
            label='Cancel'
            onClick={() => setOpen(false)}
          />,
          <PrimaryButton
            label='Archive'
            onClick={() => {
              params.archive()
              setOpen(false)
            }}
          />
        ]}
      >
        <h3>Archive this Job?</h3>
        <p>Are you sure you want to archive the job <b>{params.name}</b>?</p>
        <br />
        <p>Once archived, you will need to repost the job to start to match against new candidates.</p>
      </Modal>}
      <InlineIconLabel icon='fas fa-archive' color={Purple} onClick={() => setOpen(true)}>
        Archive
      </InlineIconLabel>
    </span>
  );
}

export default ArchiveButton
