import React from 'react'

function Modal(params) {

    let buttons = params.buttons
    if (params.buttons instanceof Array) {
        buttons = []
        buttons = params.buttons.map((node, i) => {
            return <span key={'modal-button-' + i}
                style={{ marginLeft: 12, marginRight: 12 }}>{node}</span>
        })
    }

    let textAlign = params.textAlign
    if (!textAlign) {
        textAlign = 'center'
    }

    let buttonAlign = params.buttonAlign
    if (!buttonAlign) {
        buttonAlign = textAlign
    }

    return (
        <div className="modal is-active">
            <div className="modal-background" onClick={params.closeOnClick}></div>
            <div className="modal-content" style={{ borderRadius: 12, backgroundColor: 'white', padding: 20, textAlign: textAlign }}>
                <div style={{ position: 'absolute', right: 10, top: 10, pointerEvents: 'initial', cursor: 'pointer' }}>
                    <button className="delete icon" aria-label="close" onClick={params.closeOnClick} style={{ pointerEvents: 'initial', cursor: 'pointer' }} />
                </div>
                {params.imageSrc && <img
                    src={params.imageSrc}
                    alt=''
                    width={300}
                />}
                {params.children}
                <div style={{ paddingTop: 24, textAlign: buttonAlign }}>
                    {buttons}
                </div>
            </div>
        </div>
    )
}

export default Modal