

import React from 'react'

function InlineIconLabel(params) {
    let className = ''

    if (params.onClick) {
        className = 'inline-icon-button'
    }
    return (
        <span className={className} style={{ color: params.color }} onClick={params.onClick}>
            <i className={params.icon} style={{ marginRight: 8, color: params.color }} />
            {params.children}
        </span>
    )
}

export default InlineIconLabel
