    size: '+100',
    founded: '2010',
    profile_picture: 'https://upload.wikimedia.org/wikipedia/commons/a/a4/Evernote_Icon.png',
    cover_picture: 'https://assets.entrepreneur.com/content/3x2/2000/20180906185428-ent18-oct-officespace-evernote1.jpeg',
