import React, { useRef, useState } from 'react'
import { Turquoise } from '../colors'
import Modal from '../Modal'
import InlineIconLabel from '../InlineIconLabel'
import { Purple } from '../colors'
function ShareButtonV2() {

  const [copySuccess, setCopySuccess] = useState('Copy')
  const [open, setOpen] = useState(false)
  const textAreaRef = useRef(null);

  function copyToClipboard(e) {
    textAreaRef.current.select();
    document.execCommand('copy');
    e.target.focus();
    setCopySuccess('Copied');
  };


  return (
    <span>
      {open && <Modal
        closeOnClick={() => setOpen(false)}
        textAlign='left'
        buttonAlign='right'
      >
        <h3>Share this Job</h3>
        <div className='columns' style={{ marginTop: 12 }}>
          <div className='column'>
            <Icon containerClassName='is-linkedin' className='fab fa-linkedin-in' style={{ fontSize: 28, top: 'calc(50% - 12px)', left: 'calc(50% - 12px)' }} />
          </div>
          <div className='column'>
            <Icon containerClassName='is-facebook' className='fab fa-facebook-f' style={{ fontSize: 32, top: 'calc(50% - 15px)', left: 'calc(50% - 10px)' }} />
          </div>
          <div className='column'>
            <Icon containerClassName='is-whatsapp' className='fab fa-whatsapp' style={{ fontSize: 32, top: 'calc(50% - 15px)', left: 'calc(50% - 13px)' }} />
          </div>
          <div className='column'>
            <Icon containerClassName='is-trello' className='fab fa-trello' style={{ fontSize: 32, top: 'calc(50% - 15px)', left: 'calc(50% - 14px)' }} />
          </div>
          <div className='column'>
            <Icon containerClassName='is-telegram' className='fab fa-telegram-plane' style={{ fontSize: 32, top: 'calc(50% - 15px)', left: 'calc(50% - 16px)' }} />
          </div>
          <div className='column'>
            <Icon containerClassName='is-slack' className='fab fa-slack' style={{ fontSize: 32, top: 'calc(50% - 15px)', left: 'calc(50% - 14px)' }} />
          </div>
        </div>
        <p>Or copy the link below</p>
        <div className="control has-icons-right">
          <input
            className="input"
            ref={textAreaRef}
            type="text"
            value={window.location}
          />
          <span
            className="icon is-right"
            style={{ pointerEvents: 'initial', cursor: 'pointer', marginRight: 12, color: Turquoise, fontWeight: 'bold' }}
            onClick={copyToClipboard}
          >
            {copySuccess}
          </span>
        </div>
      </Modal>}
      <InlineIconLabel icon='fas fa-share-square' color={Purple} onClick={() => setOpen(true)}>
        Share
      </InlineIconLabel>
    </span>
  );
}

export default ShareButtonV2

function Icon(params) {
  return (
    <div className={params.containerClassName}
      style={{
        width: 64,
        height: 64,
        borderRadius: 32,
        cursor: 'pointer',
      }}>
      <i style={{ color: '#FFFFFF', position: 'relative', ...params.style }} className={params.className} />
    </div>
  )
}