function App() {
  console.log('v7')
  // fetch('https://0jzvzdhkz4.execute-api.eu-west-1.amazonaws.com/prod/testing')
  //   .then(res => res.json())
  //   .then(
  //     (result) => {
  //       console.log(result)
  //     })

  const [state, dispatch] = React.useReducer(reducer, initialState)

  return (
    <Context.Provider value={{
      state,
      dispatch
    }}>
      <Router basename={process.env.REACT_APP_ROUTER_BASENAME}>
        <NavBar />

        {state.notifications.show_welcome && <AlphaModal
          closeOnClick={() => dispatch({ type: 'HIDE_WELCOME_MODAL' })}
        />}

        <section className='section' style={{ width: '100%', display: 'flex', flexDirection: 'column' }}>
          <div className='container' style={{ width: '100%', flexGrow: 1, display: 'flex', flexDirection: 'column' }}>
            <HomeProvider>
              <Route exact path='/' component={Home} />
              <Route exact path='/v2' component={HomeV2} />
            </HomeProvider>
            <Route exact path='/login' component={Login} />
            <Route exact path='/search' component={Search} />
            <Route exact path='/jobs/new' component={JobForm} />
            <Route exact path='/edit' component={EditJobForm} />
            <JobsProvider>
              <Route exact path='/job/:id' component={Job} />
            </JobsProvider>
            <Route exact path='/jobs-overview' component={JobsOverview} />
            <Route exact path='/job/:id/candidates' component={JobCandidates} />
            <Route exact path='/job/:id/candidates/:page' component={JobCandidates} />
            <Route exact path='/job/:id/candidates/:page/:profile_id' component={CandidateProfile} />
            <Route exact path='/messaging' component={Messaging} />
            <Route exact path='/messaging/:job_id' component={Messaging} />
            <Route exact path='/messaging/:job_id/:candidate_id' component={Messaging} />
            <Route exact path='/recruiter/:recruiter_id' component={RecruiterProfile} />
            <Route exact path='/company/:company_id' component={Company} />
          </div>
        </section>

        <footer className='footer'>
          <div className='content has-text-centered'>
            <p>
              {randomFooterMessages}
            </p>
          </div>
        </footer>
      </Router>
    </Context.Provider >
  );
}

export default App
