import React, { useState } from 'react'
import InlineIconLabel from '../InlineIconLabel'
import OutlineButton from './OutlineButton'
import PrimaryButton from './PrimaryButton'
import { Purple } from '../colors'
import Modal from '../Modal'

function RepostJobButton(params) {
  const [open, setOpen] = useState(false)

  return (
    <span>
      {open && <Modal
        closeOnClick={() => setOpen(false)}
        textAlign='left'
        buttonAlign='right'
        buttons={[
          <OutlineButton
            label='Cancel'
            onClick={() => setOpen(false)}
          />,
          <PrimaryButton
            label='Repost'
            onClick={() => {
              params.repost()
              setOpen(false)
            }}
          />
        ]}
      >
        <h3>Repost this Job?</h3>
        <p>Are you sure you want to repost the job <b>{params.name}</b>?</p>
        <br />
        <p>Once reposted, you will to start to match against new candidates.</p>
      </Modal>}
      <InlineIconLabel icon='fas fa-redo' color={Purple} onClick={() => setOpen(true)}>
        Repost
      </InlineIconLabel>
    </span>
  );
}

export default RepostJobButton
