export const getCurrencySymbol = (currency) => {
  if (currency === 'GBP') {
    return '£'
  }
  if (currency === 'EUR') {
    return '€'
  }
  if (currency === 'USD') {
    return '$'
  }
  return '?'
}

export const sortTechStack = tech_stack => {
  function compare(a, b) {
    if (a.name < b.name) {
      return -1;
    }
    if (a.name > b.name) {
      return 1;
    }
    return 0;
  }
  let allTech = {
    0: [],
    1: [],
    2: [],
    3: [],
  }
  tech_stack.map(tech => (
    allTech[tech.experience].push(tech)
  ))

  allTech[0].sort(compare)
  allTech[1].sort(compare)
  allTech[2].sort(compare)
  allTech[3].sort(compare)

  return [...allTech[3], ...allTech[2], ...allTech[1], ...allTech[0]]
}
