import React, { useState, useContext } from 'react'
import TextInput from './global/TextInput'
import NumberInput from './global/NumberInput'
import { LightGrey } from './global/colors'
import AutoSuggestListInput from './global/AutoSuggestListInput'
import IconButton from './global/buttons/IconButton'

import { JobCard } from './global/Cards'

import { Context } from './App'

import Breadcrumbs from './global/Breadcrumbs'
import PrimaryButton from './global/buttons/PrimaryButton'
import AutoSuggestTextInput from './global/AutoSuggestTextInput'
import DeletableTechTag from './global/tags/DeletableTechTag'
import { techLanguages, getRandomInt } from './global/mock'
import InfiniteScroll from 'react-infinite-scroll-component'
import { DeletableTag } from './global/Tags'

function Search() {

  const { state } = useContext(Context)

  const [searchWhat, setSearchWhat] = useState(state.search.what)
  const [searchWhere, setSearchWhere] = useState(state.search.where)

  const [otherBenefits, setOtherBenefits] = useState([])

  const [minSalary, setMinSalary] = useState(50000)
  const [maxSalary, setMaxSalary] = useState(70000)

  const onClickOtherBenefitsInput = function (value) {
    let copiedOtherBenefits = [...otherBenefits]
    copiedOtherBenefits.push(value)
    setOtherBenefits(copiedOtherBenefits)
  }

  const [tech, setTech] = useState([])

  const onClickTech = function (value) {
    let techStack = [...tech]
    techStack.push(value)
    setTech(techStack)
  }

  const onDeleteTech = function (index) {
    return function (event) {
      let techStack = [...tech]
      techStack.splice(index, 1)
      setTech(techStack)
    }
  }

  const onDeleteOtherBenefits = function (index) {
    return function (event) {
      let techStack = [...otherBenefits]
      techStack.splice(index, 1)
      setOtherBenefits(techStack)
    }
  }

  const [items, setItems] = useState(Array.from({ length: 24 }))

  const fetchMoreData = () => {
    setTimeout(() => {
      setItems(items.concat(Array.from({ length: 24 })))
    }, getRandomInt(500, 2500))
  }

  return (
    <div>
      <Breadcrumbs links={[
        {
          name: 'Home',
          href: '/'
        },
        {
          name: 'Results for Java Developer in London, UK',
        },
      ]} />
      <div className='columns'>

        <div className='column is-3 job-search-filters'>

        </div>
        <div className='column'>
          <div className='columns'>
            <div className='column'>
              <TextInput
                value={searchWhat}
                onChange={event => setSearchWhat(event.target.value)}
              />
            </div>
            <div className='column'>
              <AutoSuggestTextInput
                value={searchWhere}
                placeholder='London, UK'
                addToList={setSearchWhere}
                data={state.dictionary.locations}
              />
            </div>
            <div className='column'>
              <PrimaryButton
                label='See Relevant Positions'
              />
            </div>
          </div>
        </div>
      </div>

      <div className='columns'>

        <div className='column is-3 job-search-filters'>

          <h5 style={{ marginBottom: 8 }}>Skills</h5>
          <AutoSuggestListInput
            placeholder='Type a technology'
            addToList={onClickTech}
            data={techLanguages}
          />
          <div style={{ paddingTop: 4 }}>
            {tech && tech.map((text, i) => (

              <DeletableTag
                onClick={onDeleteTech(i)}
              >
                {text}
              </DeletableTag>
            ))}
          </div>

          <div>
            <h5 style={{ marginTop: 48, marginBottom: 8 }}>Annual Salary</h5>
            <div className='columns' style={{ marginBottom: 0, paddingBottom: 0 }}>
              <div className='column is-5' style={{ marginBottom: 0, paddingBottom: 0 }}>
                <div style={{ fontWeight: 'bold', color: LightGrey }}>
                  From
                </div>
              </div>
              <div className='column is-1' style={{ marginBottom: 0, paddingBottom: 0 }}>
              </div>
              <div className='column is-5' style={{ marginBottom: 0, paddingBottom: 0 }}>
                <div style={{ fontWeight: 'bold', color: LightGrey }}>
                  To
                </div>
              </div>
            </div>

            <div className='columns'>
              <div className='column is-5'>
                <div className='control has-icons-left'>
                  <NumberInput
                    value={minSalary}
                    onChange={event => setMinSalary(event.target.value)}
                  />
                  <span className='icon is-small is-left'>
                    £
                    {/* {getCurrencySymbol(params.currency)} */}
                  </span>
                </div>
              </div>
              <div className='column is-1' style={{ borderBottom: '1px solid ' + LightGrey, height: 32 }}>
              </div>
              <div className='column is-5'>
                <div className='control has-icons-left'>
                  <NumberInput
                    value={maxSalary}
                    onChange={event => setMaxSalary(event.target.value)}
                  />
                  <span className='icon is-small is-left'>
                    £
                    {/* {getCurrencySymbol(params.currency)} */}
                  </span>
                </div>
              </div>
            </div>
          </div>

          <h5 style={{ marginTop: 48, marginBottom: 8 }}>Job Type</h5>
          <CheckboxOptions
            options={['Full time', 'Contract', 'Part time', 'Temp', 'Student Placement', 'Co-Founder']}
          />


          <h5 style={{ marginTop: 36, marginBottom: 8 }}>Other</h5>
          <CheckboxOptions
            options={['Fully remote', 'Verified profiles', 'Top recruiters']}
          />

          <h5 style={{ marginTop: 36, marginBottom: 8 }}>Benefits</h5>
          <AutoSuggestListInput
            placeholder='You can have as many as you like!'
            addToList={onClickOtherBenefitsInput}
            data={state.dictionary.otherBenefits}
          />
          <div style={{ paddingTop: 4 }}>
            {otherBenefits && otherBenefits.map((text, i) => (

              <DeletableTag
                onClick={onDeleteOtherBenefits(i)}
              >
                {text}
              </DeletableTag>
            ))}
          </div>
        </div>

        <div className='column'>
          <h3>Java Developer (96)</h3>
          <InfiniteScroll
            dataLength={items.length}
            next={fetchMoreData}
            hasMore={true}
            loader={<div className='column is-12 has-text-centered has-text-secondary is-size-2'>
              <i class='fas fa-circle-notch fa-spin'></i>
            </div>}
            className='columns is-multiline is-mobile has-space-between'
          >
            {items.map((i, index) => (
              <JobCard
                key={index}
                position='Senior Fullstack Developer'
                picture='https://www.telegraph.co.uk/content/dam/technology/2016/05/03/Face_swap_fail_18_trans_NvBQzQNjv4BqtM3ZXxV42fQENILgObxjOFzt7SZsQQ7h3oKyATfgi0w.jpg?imwidth=48'
                from='Bobert'
                location='London, UK'
                views={23}
                days_ago={15}
                tag={
                  <JobSearchResultTag
                    value='TOP RECRUITER'
                  />
                }
              />
            ))}
          </InfiniteScroll>
        </div>

      </div>


    </div>
  );
}

export default Search

function JobSearchResultTag(params) {
  return (
    <span className='job-search-result-tag' style={{}}>
      {params.value}
    </span>
  )
}

function CheckboxOptions(params) {
  return (
    <div className='columns is-multiline is-mobile' style={{ paddingLeft: 12, paddingRight: 4 }}>
      {params.options.map((option, index) => (
        <div className='column' key={index} style={{ flex: 'auto', paddingTop: 4, paddingBottom: 4, paddingLeft: 0, paddingRight: 0, marginTop: 0, marginBottom: 0 }}>
          <input class='is-checkradio is-primary' id={option.replace(/^[^a-z]+|[^\w:.-]+/gi, '') + 'Checkbox'} type='checkbox' name='exampleCheckbox' />
          <label for={option.replace(/^[^a-z]+|[^\w:.-]+/gi, '') + 'Checkbox'}>{option}</label>
        </div>
      ))}
    </div>
  )
}