import React, { useState } from 'react'
import { Link, useParams } from 'react-router-dom'

import Tabs from './global/Tabs'
import Controls from './global/Controls'
import PrimaryButton from './global/buttons/PrimaryButton'
import OutlineButton from './global/buttons/OutlineButton'
import { Turquoise } from './global/colors'
import InlineIconLabel from './global/InlineIconLabel'
import Avatar from './global/Avatar'
import Modal from './global/Modal'
import {
  RouteJobCandidatesNewMatches,
  RouteJobCandidatesInProcess,
  RouteJobCandidatesCandidatesAccepted,
  RouteJobsOverview
} from './global/routes'
import { Context } from "./App"
import Breadcrumbs from './global/Breadcrumbs'
import { sortTechStack } from './global/functions'

function JobCandidates(params) {
  const { state, dispatch } = React.useContext(Context)

  const { id, page } = useParams()
  let defaultTab = 0
  if (!page) {
    defaultTab = 0
    params.history.push({
      pathname: RouteJobCandidatesNewMatches(id),
    })
  }
  if (page === 'in-process') {
    defaultTab = 1
  }
  if (page === 'candidates-accepted') {
    defaultTab = 2
  }
  const [index, setIndex] = useState(defaultTab)

  let data = state.recruiter.activeJobs[id]
  if (!data) {
    return (
      <div>Make an error page and tell the user they can't see this job's candidates</div>
    )
  }
  Object.keys(data.new_matches).map(candidateId => (
    data.new_matches[candidateId].tech_stack = sortTechStack(data.new_matches[candidateId].tech_stack)
  ))
  Object.keys(data.in_process).map(candidateId => (
    data.in_process[candidateId].tech_stack = sortTechStack(data.in_process[candidateId].tech_stack)
  ))
  Object.keys(data.accepted_candidates).map(candidateId => (
    data.accepted_candidates[candidateId].tech_stack = sortTechStack(data.accepted_candidates[candidateId].tech_stack)
  ))

  const setTab = index => {
    if (index === 0) {
      params.history.push({
        pathname: RouteJobCandidatesNewMatches(id),
      })
    }
    if (index === 1) {
      params.history.push({
        pathname: RouteJobCandidatesInProcess(id),
      })
    }
    if (index === 2) {
      params.history.push({
        pathname: RouteJobCandidatesCandidatesAccepted(id),
      })
    }
    setIndex(index)
  }

  return (
    <div>
      {state.notifications.show_match_modal && <Modal
        closeOnClick={() => dispatch({ type: 'HIDE_MATCH_MODAL' })}
        imageSrc={process.env.PUBLIC_URL + '/choose.svg'}
        buttons={[
          <OutlineButton
            label='Back to Candidates'
            onClick={() => dispatch({ type: 'HIDE_MATCH_MODAL' })}
          />,
          <Link to={'/messaging/' + id + '/' + state.notifications.candidate_id_match_modal}>
            <PrimaryButton
              label='Send Message'
              onClick={() => {
                dispatch({ type: 'HIDE_MATCH_MODAL' })
              }}
            />
          </Link>
        ]}
      >
        <h2>It's a Match!</h2>
        <p>
          Looks like things are moving quickly. Send a message to start this process.
        </p>
      </Modal>}

      <Breadcrumbs links={[
        {
          name: 'Jobs Overview',
          href: RouteJobsOverview()
        },
        {
          name: data.position,
        },
      ]} />

      <h1>{data.position}</h1>
      <div className='columns' style={{ marginTop: 12 }}>
        <div className='column is-8'>
          <Tabs
            tabs={[
              'New Matches (' + Object.keys(data.new_matches).length + ')',
              'In Process (' + Object.keys(data.in_process).length + ')',
              'Candidates Accepted (' + Object.keys(data.accepted_candidates).length + ')'
            ]}
            position={index}
            setPosition={setTab}
          />
        </div>
      </div>


      {index === 0 && data.new_matches && Object.keys(data.new_matches).map(candidateId => (
        <Candidate candidate={data.new_matches[candidateId]} id={id} page={page}>
          <Controls
            right={[
              <InlineIconLabel icon='fas fa-thumbs-up' onClick={() => dispatch({ type: 'QUICK_MATCH_CANDIDATE', jobId: id, candidateId: candidateId })}>
                Quick Match
              </InlineIconLabel>
            ]}
          />
        </Candidate>
      ))
      }

      {
        index === 1 && data.in_process && Object.keys(data.in_process).map(candidateId => (
          <Candidate candidate={data.in_process[candidateId]} id={id} page={page}>
            <div style={{ position: 'relative', height: '100%' }}>
              <Controls
                right={[
                  <InlineIconLabel
                    icon='fas fa-file-alt'
                    onClick={() => dispatch({ type: 'REQUEST_CV', jobId: id, candidateId: candidateId })}>
                    {data.in_process[candidateId].cv_requested && <span>CV Requested</span>}
                    {!data.in_process[candidateId].cv_requested && <span>Request CV</span>}
                  </InlineIconLabel>,
                  <Link className='inline-icon-button' to={'/messaging/' + id + '/' + candidateId}>
                    <InlineIconLabel icon='fas fa-comments'>
                      Show Conversation
                    </InlineIconLabel>
                  </Link>
                ]}
              />
              <div style={{ position: 'absolute', bottom: 0, textAlign: 'right', width: '100%' }}>
                <InlineIconLabel icon='fas fa-times' onClick={() => dispatch({ type: 'CANCEL_PROCESS_FOR_CANDIDATE', jobId: id, candidateId: candidateId })}>
                  Cancel Process
                </InlineIconLabel>
              </div>
            </div>
          </Candidate>
        ))
      }

      {
        index === 2 && data.accepted_candidates && Object.keys(data.accepted_candidates).map(candidateId => (
          <Candidate candidate={data.accepted_candidates[candidateId]} id={id} page={page}>
            <div style={{ position: 'relative', height: '100%' }}>
              <Controls
                right={[
                  <a className='inline-icon-button' download href={process.env.PUBLIC_URL + '/cv.pdf'} target='_blank' rel="noopener noreferrer">
                    <InlineIconLabel icon='fas fa-download'>
                      Download CV
                    </InlineIconLabel>
                  </a>,
                  <Link className='inline-icon-button' to={'/messaging/' + id + '/' + candidateId}>
                    <InlineIconLabel icon='fas fa-comments'>
                      Show Conversation
                    </InlineIconLabel>
                  </Link>
                ]}
              />
            </div>
          </Candidate>
        ))
      }

    </div >
  )
}

export default JobCandidates

function Candidate(params) {

  const [showMore, setShowMore] = useState(false)

  return (
    <div style={{ borderBottom: '1px solid #E4E4E4', marginTop: 24 }}>
      <div className='columns is-gapless is-marginless'>
        <div className='column is-1'>
          <Link to={'/job/' + params.id + '/candidates/' + params.page + '/' + params.candidate.id}>
            <Avatar size={64} src={params.candidate.profile_picture} />
          </Link>
        </div>
        <div className='column is-8' style={{ marginBottom: 12 }}>
          <div>
            <div>
              <Link to={'/job/' + params.id + '/candidates/' + params.page + '/' + params.candidate.id}>
                <h3 style={{ display: 'inline', marginRight: 12 }}>{params.candidate.name}</h3>
              </Link>
              <InlineIconLabel icon='fas fa-map-marker' color='#D1D1D6'>
                {params.candidate.location}
              </InlineIconLabel>
            </div>
            <div>
              {params.candidate.tech_stack && params.candidate.tech_stack.slice(0, 3).map(tech => (
                <div style={{
                  backgroundColor: `${tech.experience === 3 ? '#7F4CDC' : ''}${tech.experience === 2 ? '#A079E5' : ''}${tech.experience === 1 ? '#CCB8F1' : ''}`,
                  display: 'inline-block',
                  color: '#FFFFFF',
                  fontWeight: 'bold',
                  margin: 4,
                  paddingTop: 4,
                  paddingBottom: 4,
                  paddingLeft: 12,
                  paddingRight: 12,
                }}>{tech.name}</div>
              ))}
            </div>
            <div>
              {!showMore && params.candidate.work && params.candidate.work.slice(0, 2).map(work => (
                <div>{work.role} - {work.company.name} | {work.start_date} - {work.end_date}</div>
              ))}

              {!showMore && params.candidate.work && params.candidate.work.length > 2 && <div
                style={{ fontWeight: 'bold', cursor: 'pointer', color: Turquoise }}
                onClick={event => setShowMore(!showMore)}
              >
                Show {params.candidate.work.length - 2} More Experiences <i className='fas fa-chevron-down' />
              </div>}

              {showMore && params.candidate.work && params.candidate.work.map(work => (
                <div>{work.role} - {work.company.name} | {work.start_date} - {work.end_date}</div>
              ))}

              {showMore && params.candidate.work && params.candidate.work.length > 2 && <div
                style={{ fontWeight: 'bold', cursor: 'pointer', color: Turquoise }}
                onClick={event => setShowMore(!showMore)}
              >
                Show Less Experiences <i className='fas fa-chevron-up' />
              </div>}
            </div>
          </div>
        </div>
        <div className='column is-3' style={{ textAlign: 'right', marginTop: 16 }}>
          {params.children}
        </div>
      </div>
    </div >
  )
}