import React from 'react'

export function Tag(params) {
  return (
    <div style={{
      backgroundColor: '#956EDC',
      display: 'inline',
      color: '#FFFFFF',
      fontWeight: 'bold',
      margin: 4,
      paddingTop: 4,
      paddingBottom: 4,
      paddingLeft: 12,
      paddingRight: 12,
    }}>
      {params.children}
    </div>
  )
}

export function DeletableTag(params) {
  return (
    <div style={{
      backgroundColor: '#956EDC',
      display: 'inline-block',
      color: '#FFFFFF',
      fontWeight: 'bold',
      margin: 4,
      paddingTop: 4,
      paddingBottom: 4,
      paddingLeft: 12,
      paddingRight: 12,
    }}
    >
      {params.children}
      <span className="icon" style={{ pointerEvents: 'initial', cursor: 'pointer' }} onClick={params.onClick}>
        <i
          className='fas fa-times'
          style={{ paddingTop: 4 }}
        />
      </span>
    </div>
  )
}

