import React from 'react'
import Avatar from './Avatar'
import { Link } from 'react-router-dom'
import { Tag } from './Tags'

export function JobCard(params) {
    return (
        <div className='column job-card' style={{ minWidth: 280, maxWidth: 280, margin: 16, textAlign: 'left' }}>
            <Link className='card-link' to='/job/abc'>
                <nav className='level' style={{ marginBottom: 8 }}>
                    <div className='level-left'>
                        <div className='level-item'>
                            <Avatar size={48} src={params.picture} />
                        </div>
                    </div>

                    <div className='level-right'>
                        <div className='level-item'>
                            {params.tag}
                        </div>
                    </div>
                </nav>
                <h5>{params.position}</h5>
                <span className='has-text-grey-one' style={{ marginTop: 4 }}>{params.from}, {params.location}</span>
                <div className='job-search-result-card-footer'>
                    {params.views} views ● Posted {params.days_ago} days ago
                </div>
            </Link>
        </div>
    )
}

