export function HomeRecruiterCard(params) {
    return (
        <div className='column job-card' style={{ minWidth: 280, maxWidth: 280, margin: 16, textAlign: 'left' }}>
            <Link className='card-link' to='/recruiter/abc'>
                <div className='has-text-centered'>
                    <Avatar size={80} src={params.picture} />

                </div>
                <div className='has-text-centered' style={{ margin: 8 }}>
                    {[1, 2, 3, 4, 5].map((name, index) => (
                        <span key={index}><i className='fas fa-star has-text-primary' /></span>
                    ))}
                </div>
                <h5>{params.name}</h5>
                <div className='has-text-grey-one' style={{ marginTop: 4 }}>{params.position}</div>

                <div className='job-search-result-card-footer'>
                    <div><b>Live roles: <span className='has-text-secondary'>{params.live_roles}</span></b></div>
                    <div><b>Active: <span className='has-text-secondary'>{params.active}</span></b></div>
                </div>
            </Link>
        </div>
    )
}

export function HomeCompanyCard(params) {
    return (
        <div className='column job-card' style={{ minWidth: 400, maxWidth: 350, margin: 16, textAlign: 'left', padding: 0 }}>
            <Link className='card-link' to={'/company/' + params.id}>
                <img src={params.picture} alt='Avatar' style={{ width: '100%', height: 125, borderRadius: '9px 9px 0px 0px', objectFit: 'cover' }}></img>
                <div style={{ padding: 16 }}>
                    <h5>{params.name}</h5>

                    <div style={{ marginTop: 10, marginLeft: -4 }}>
                        {params.tech.map(tech => (
                            <Tag>{tech}</Tag>
                        ))}
                    </div>
                </div>
            </Link>
        </div>
    )
}
