import React, { useState, useContext } from 'react'

import PrimaryButton from './global/buttons/PrimaryButton'
import PasswordInput from './global/PasswordInput'
import TextInput from './global/TextInput'
import { Context } from './App'

function Login(params) {
  const { state, dispatch } = useContext(Context)

  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')

  return (
    <div>
      <TextInput
        value={email}
        placeholder='Email address'
        onChange={event => setEmail(event.target.value)}
      />
      <PasswordInput
        value={password}
        placeholder='Password'
        onChange={event => setPassword(event.target.value)}
      />
      <PrimaryButton
        label='Log in'
        onClick={() => {
          fetch('http://localhost:8000/auth/email', {
            method: 'POST',
            body: JSON.stringify({ email: email, password: password })
          })
            .then(res => res.json())
            .then(
              (result) => {
                dispatch({ type: 'LOGIN', token: result.token, user_type: result.type })
                // homeState.dispatch(fetched(result))
              })
        }}
      />
    </div>
  );
}

export default Login