import React from 'react'
import { Link } from 'react-router-dom';

function Breadcrumbs(params) {

    return (
        <nav className='breadcrumb' aria-label='breadcrumbs'>
            <ul>
                {params.links && params.links.map((link, i) => (
                    <li key={'breadcrumb-' + i} className={i === params.links.length - 1 ? 'is-active' : ''}>
                        {link.href && <Link to={link.href} className='breadcrumb-link'>{link.name}</Link>}
                        {!link.href && <Link to='#'><div className='breadcrumb-link'>{link.name}</div></Link>}
                    </li>
                ))}
            </ul>
        </nav>
    );
}

export default Breadcrumbs