import React, { useContext } from 'react'
import DeletableTechTag from './global/tags/DeletableTechTag'
import RadioInput from './RadioInput'
import AutoSuggestTextInput from './global/AutoSuggestTextInput'
import AutoSuggestListInput from './global/AutoSuggestListInput'
import { techLanguages } from './global/mock'
import { Context } from "./App"

function PostBasicInfo(params) {
  const { state } = useContext(Context)

  const onDeleteTechStack = function (index) {
    return function (event) {
      let techStack = [...params.techStack]
      techStack.splice(index, 1);
      params.setTechStack(techStack)
    }
  }

  const onClickBenefitsInput = function (value) {
    params.setLocation(value)
  }

  const onClickOtherBenefitsInput = function (value) {
    let techStack = [...params.techStack]
    techStack.push(value)
    params.setTechStack(techStack)
  }

  return (
    <div>
      <div className="columns">
        <div className="column is-6">
          <h3>Job Title</h3>
          <input
            className="input"
            type="text"
            placeholder="Java developer"
            onChange={event => params.setPosition(event.target.value)}
            value={params.position}
          />
        </div>
      </div>
      <div className="columns">
        <div className="column is-6">
          <h3>Tech Stack</h3>

          <AutoSuggestListInput
            placeholder='Type a technology'
            addToList={onClickOtherBenefitsInput}
            data={techLanguages}
          />
          {params.techStack && params.techStack.map((text, i) => (
            <DeletableTechTag
              key={text}
              label={text}
              onClick={onDeleteTechStack(i)}
            />
          ))}

        </div>
      </div>
      <div className="columns">
        <div className="column is-6">
          <h3>Location</h3>
          <AutoSuggestTextInput
            value={params.location}
            placeholder='London, UK'
            addToList={onClickBenefitsInput}
            data={state.dictionary.locations}
          />
        </div>
      </div>
      <h3>Employment Type</h3>
      <RadioInput
        name='employmentType'
        value={params.employmentType}
        onChange={params.setEmploymentType}
        options={[
          {
            label: 'Permanent',
            value: 'permanent',
            checked: () => params.employmentType === 'permanent',
            onChange: event => params.setEmploymentType(event.target.value)
          },
          {
            label: 'Freelance',
            value: 'freelance',
            checked: () => params.employmentType === 'freelance',
            onChange: event => params.setEmploymentType(event.target.value)
          },
          {
            label: 'Part-time',
            value: 'parttime',
            checked: () => params.employmentType === 'parttime',
            onChange: event => params.setEmploymentType(event.target.value)
          },
          {
            label: 'Internship',
            value: 'internship',
            checked: () => params.employmentType === 'internship',
            onChange: event => params.setEmploymentType(event.target.value)
          }]
        }
      />
      <h3>Remote Working</h3>
      <RadioInput
        name='remote'
        value={params.remoteWorking}
        onChange={params.setRemoteWorking}
        options={[
          {
            label: 'Fully',
            value: 'fully'
          },
          {
            label: 'Mostly',
            value: 'mostly'
          },
          {
            label: 'Once a week',
            value: 'once'
          },
          {
            label: 'When needed',
            value: 'when'
          },
          {
            label: 'Very rarely',
            value: 'rarely'
          },
          {
            label: 'None',
            value: 'none'
          }]
        }
      />

      <h3>Business Travel</h3>
      <RadioInput
        name='businessTravel'
        value={params.businessTravel}
        onChange={params.setBusinessTravel}
        options={[
          {
            label: '100%',
            value: '100'
          },
          {
            label: '75%',
            value: '75'
          },
          {
            label: '50%',
            value: '50'
          },
          {
            label: '25%',
            value: '25'
          },
          {
            label: 'None',
            value: '0'
          }]
        }
      />

    </div >
  );
}

export default PostBasicInfo;