import React, { useEffect } from 'react'

import JobViewRecruiterSubSection from './job/JobViewRecruiterSubSection'
import JobViewExtraInfoSubSection from './job/JobViewExtraInfoSubSection'
import JobViewSocialSubSection from './job/JobViewSocialSubSection'
import JobTab from './job/JobTab'
import BenefitsTab from './job/BenefitsTab'
import CompanyTab from './job/CompanyTab'

import { LightGrey } from './global/colors'
import { job } from './global/mock'
import Tabs from './global/Tabs'
import Controls from './global/Controls'
import Modal from './global/Modal'
import PrimaryButton from './global/buttons/PrimaryButton'
import OutlineButton from './global/buttons/OutlineButton'
import ReportButton from './global/buttons/ReportButton'
import ShareButton from './global/buttons/ShareButton'
import SaveButton from './global/buttons/SaveButton'
import Breadcrumbs from './global/Breadcrumbs'
import { useHistory } from 'react-router-dom'
import { store as jobStore, fetching, fetched } from './global/state/Jobs'
import { Context } from './App'

function Job(params) {
  const { state, dispatch } = React.useContext(Context)
  const jobState = React.useContext(jobStore)
  const [index, setIndex] = React.useState(0)

  useEffect(() => {
    jobState.dispatch(fetching())
    fetch('http://localhost:8000/jobs/job-123')
      .then(res => res.json())
      .then(
        (result) => {
          jobState.dispatch(fetched('abc', result))
        })
  }, [])

  if (!params.job) {
    params = {}
    if (jobState.state.jobs['abc']) {
      params.job = jobState.state.jobs["abc"]
      params.job.company = job.company
      params.job.recruiter = job.recruiter
    } else {
      params.job = job
    }
  }

  const [showMatchModal, setShowMatchModal] = React.useState(false);

  const matchClicked = function () {
    setShowMatchModal(true)
  }
  let history = useHistory();
  return (
    <div>

      <Breadcrumbs links={[
        {
          name: 'Home',
          href: '/'
        },
        {
          name: params.job.position,
        },
      ]} />

      {showMatchModal && <Modal
        closeOnClick={() => setShowMatchModal(false)}
        imageSrc={process.env.PUBLIC_URL + '/shakinghands.svg'}
        buttons={[
          <OutlineButton
            label='Back to Job Board'
          />,
          <PrimaryButton
            label='See Matches'
          />
        ]}
      >
        <h2>Awesome!</h2>
        <p>
          Your profile will be put forward to this position. If matched, you will be notified.
        </p>
      </Modal>}
      {showMatchModal && <Modal
        closeOnClick={() => setShowMatchModal(false)}
        imageSrc={process.env.PUBLIC_URL + '/choose.svg'}
        buttons={[
          <OutlineButton
            label='Back to Job Board'
            onClick={() => setShowMatchModal(false)}
          />,
          <PrimaryButton
            label='Send Message'
            onClick={() => history.push('/messaging/junior-developer-4932/4458e4b8-2174-4ca4-a075-575c1bf13d4d')}
          />
        ]}
      >
        <h2>It's a Match!</h2>
        <p>
          Looks like things are moving quickly. Send a message to start this process.
        </p>
      </Modal>}
      {!params.isPreview && <div />}
      <nav className="navbar is-fixed-bottom is-hidden-tablet" style={{ borderTop: '2px solid #E0E0E0' }}>
        <span className="navbar-item">
          <PrimaryButton
            label='Match'
            onClick={() => { matchClicked() }}
            className='is-fullwidth'
          />
        </span>
      </nav>
      <div style={{ marginBottom: 12 }}>
        <h1>{params.job.position}</h1>
        <h2 style={{ color: LightGrey }}>{params.job.company.name}</h2>
      </div>
      <div className="columns is-hidden-mobile" style={{ padding: 12 }}>
        <div className="column is-7">
          <Tabs
            tabs={['The Job', 'The Benefits', 'The Company']}
            position={index}
            setPosition={setIndex}
          />
        </div>
      </div>
      <div className="is-hidden-tablet" style={{ margin: 12 }}>
        <Tabs
          tabs={['The Job', 'The Benefits', 'The Company']}
          position={index}
          setPosition={setIndex}
        />
      </div>
      <div className="columns">
        <div className="column is-7">
          {(!index || index === 0) && <JobTab
            description={params.job.description}
            responsibilities={params.job.responsibilities}
            experiences={params.job.experiences}
            businessTravel={params.job.businessTravel}
          />}
          {index === 1 && <BenefitsTab
            benefits={params.job.benefits}
          />}

          {index === 2 && <CompanyTab
            company={params.job.company}
          />}
        </div>
        <div className="column is-2 is-hidden-mobile">
          <div style={{ marginBottom: 20 }}>
            <h6>Salary</h6>
            <div>£40 - £50k /yr</div>
          </div>
          <div style={{ marginBottom: 20 }}>
            <h6>Business Travel</h6>
            <div>50%</div>
          </div>
          <div style={{ marginBottom: 20 }}>
            <h6>Level</h6>
            <div>Junior / Mid</div>
          </div>
          <div style={{ marginBottom: 20 }}>
            <h6>Employment</h6>
            <div>Permanent</div>
          </div>
          <div style={{ marginBottom: 20 }}>
            <h6>Function</h6>
            <div>Developer</div>
          </div>
        </div>
        <div className="column is-2 is-hidden-tablet">
          <div>
            <div className='columns is-mobile'>
              <div className='column is-6'>
                <h6>Salary</h6>
                <div>£40 - £50k /yr</div>
              </div>
              <div className='column is-6'>
                <h6>Business Travel</h6>
                <div>50%</div>
              </div>
            </div>
            <div className='columns is-mobile'>
              <div className='column is-6'>
                <h6>Level</h6>
                <div>Junior / Mid</div>
              </div>
              <div className='column is-6'>
                <h6>Employment</h6>
                <div>Permanent</div>
              </div>
            </div>
            <div className='columns is-mobile'>
              <div className='column is-6'>
                <h6>Function</h6>
                <div>Developer</div>
              </div>
            </div>
          </div>

        </div>
        <div className="column is-3">
          {!params.isPreview && <div style={{ marginBottom: 12 }}>
            <div className="is-hidden-mobile">
              <Controls
                left={<PrimaryButton
                  label='Match'
                  onClick={() => { matchClicked() }}
                />}
                right={[
                  // <SaveButton />,
                  <ShareButton />,
                  <ReportButton />]}
              />
            </div>
            <div className="columns is-hidden-tablet is-mobile">
              <div className="column">
                <SaveButton />
              </div>
              <div className="column">
                <ShareButton />
              </div>
              <div className="column">
                <ReportButton />
              </div>
            </div>
          </div>}

          <JobViewExtraInfoSubSection
            website={params.job.company.website}
            techStack={params.job.company.techStack}
            location={params.job.company.location}
          />

          <JobViewSocialSubSection social={params.job.company.socialMedia} />

          <JobViewRecruiterSubSection
            recruiter={params.job.recruiter}
          />
        </div>
      </div>


    </div>
  );
}

export default Job;
