import React, { createContext, useReducer } from 'react'

const initialState = {
    fetching: true,
    sections: []
}

const store = createContext(initialState)
const { Provider } = store

const HomeProvider = ({ children }) => {
    const [state, dispatch] = useReducer((state, action) => {
        switch (action.type) {
            case 'FETCHING':
                return {
                    ...state,
                    fetching: true,
                }
            case 'FETCHED':
                console.log(action.res)
                return {
                    sections: action.res,
                    fetching: false,
                }
            default:
                throw new Error()
        };
    }, initialState)

    return <Provider value={{ state, dispatch }}>{children}</Provider>
};

const fetching = () => {
    return { type: 'FETCHING' }
}

const fetched = (res) => {
    return { type: 'FETCHED', res: res }
}

export { store, fetching, fetched, HomeProvider }