import React, { useState } from 'react'
import InlineIconLabel from '../InlineIconLabel'
import OutlineButton from './OutlineButton'
import PrimaryButton from './PrimaryButton'
import { Purple } from '../colors'
import Modal from '../Modal'

function DeleteJobButton(params) {
  const [open, setOpen] = useState(false)

  return (
    <span>
      {open && <Modal
        closeOnClick={() => setOpen(false)}
        textAlign='left'
        buttonAlign='right'
        buttons={[
          <OutlineButton
            label='Cancel'
            onClick={() => setOpen(false)}
          />,
          <PrimaryButton
            label='Delete'
            onClick={() => {
              params.delete()
              setOpen(false)
            }}
          />
        ]}
      >
        <h3>Delete this Job?</h3>
        <p>Are you sure you want to delete the job <b>{params.name}</b>?</p>
        <br />
        <p>Once deleted, all traces of this job will be lost forever.</p>
      </Modal>}
      <InlineIconLabel icon='fas fa-trash' color={Purple} onClick={() => setOpen(true)}>
        Delete
      </InlineIconLabel>
    </span>
  );
}

export default DeleteJobButton
