import React, { useState } from 'react'
import './Home.sass'
import { Link } from 'react-router-dom'
import { HomeJobCard, HomeRecruiterCard, HomeCompanyCard } from './global/Cards'
import TextInput from './global/TextInput'
import PrimaryButton from './global/buttons/PrimaryButton'
import { Context } from './App'
import AutoSuggestTextInput from './global/AutoSuggestTextInput'

function Home() {
  const { state, dispatch } = React.useContext(Context)
  const [searchWhat, setSearchWhat] = useState('')
  const [searchWhere, setSearchWhere] = useState('')

  return (
    <div>
      <div class="notification is-purple has-text-white is-hidden-tablet">
        Currently, Sboj is a work in progress. This sadly means that we are currently not mobile ready yet. This is a priority for us. Check back soon.
      </div>

      <section className='hero is-primary' style={{ paddingLeft: 24, paddingRight: 24 }}>
        <div className='hero-body'>
          <h1>Its a job board.</h1>
          <h1>Just in the 21st century.</h1>
          <h5 style={{ marginTop: 16, marginBottom: 16 }}>
            Apply to a job without doxing yourself.
          </h5>
          <nav className='level' style={{ margin: '0 -12px' }}>
            <div className='level-item' style={{ margin: '0 12px' }}>
              <div style={{ width: '100%' }}>
                <TextInput
                  value={searchWhat}
                  placeholder='What'
                  onChange={event => setSearchWhat(event.target.value)}
                />
              </div>
            </div>
            <div className='level-item' style={{ margin: '12px 12px' }}>
              <div style={{ width: '100%' }}>
                <AutoSuggestTextInput
                  value={searchWhere}
                  placeholder='Where'
                  addToList={setSearchWhere}
                  data={state.dictionary.locations}
                />
              </div>
            </div>
            <div className='level-item' style={{ margin: '0 12px', display: 'block' }}>
              <Link to='/search' className='no-underline'>
                <PrimaryButton
                  id='search-hero-button'
                  label='Search'
                  onClick={() => dispatch({ type: 'SEARCH', what: searchWhat, where: searchWhere })}
                />
              </Link>
            </div>
          </nav>
        </div>
      </section>
      {state.homepage.sections.map((section) => (
        <div>
          <h2 style={{ marginBottom: 22, marginTop: 44 }}>{section.name}</h2>
          <div className='columns is-multiline has-space-between'>
            {section.companies && section.companies.map((company) => (
              <HomeCompanyCard
                id={company.id}
                name={company.name}
                picture={company.picture}
                tech={company.tech}
              />
            ))}
            {section.jobs && section.jobs.map((job) => (
              <HomeJobCard
                position={job.position}
                picture={job.picture}
                from={job.from}
                location={job.location}
                views={job.views}
                days_ago={job.days_ago}
              />
            ))}
            {section.recruiters && section.recruiters.map((recruiter) => (
              <HomeRecruiterCard
                name={recruiter.name}
                position={recruiter.position}
                picture={recruiter.picture}
                live_roles={recruiter.active_roles}
                active={recruiter.active}
              />
            ))}
          </div>
        </div>
      ))}
    </div >
  );
}

export default Home;
