import React, { useState } from 'react'
import { Link } from 'react-router-dom'

import Tabs from './global/Tabs'
import PrimaryButton from './global/buttons/PrimaryButton'
import ShareButtonV2 from './global/buttons/ShareButtonV2'
import EditButtonV2 from './global/buttons/EditButtonV2'
import ArchiveButton from './global/buttons/ArchiveButton'
import DeleteJobButton from './global/buttons/DeleteJobButton'
import RepostJobButton from './global/buttons/RepostJobButton'
import Breadcrumbs from './global/Breadcrumbs'
import InlineIconLabel from './global/InlineIconLabel'
import {
  RouteJobCandidatesNewMatches,
  RouteJobCandidatesInProcess,
  RouteJobCandidatesCandidatesAccepted
} from './global/routes'
import { Context } from "./App"

const searchArray = (array, search, sortBy) => {
  let newArray = []
  Object.keys(array).filter(key => {
    if (!search || search === 0) {
      newArray.push(array[key])
    } else {
      if (array[key].position.toLowerCase().includes(search)) {
        newArray.push(array[key])
      }
    }
  })
  if (sortBy === 'Most Recent') {
    newArray.sort(function (a, b) { return a.days_until_expiry < b.days_until_expiry });
  }

  if (sortBy === 'Most New Matches') {
    newArray.sort(function (a, b) { return Object.keys(b.new_matches).length - Object.keys(a.new_matches).length });
  }

  if (sortBy === 'Most In Process') {
    newArray.sort(function (a, b) { return Object.keys(b.in_process).length - Object.keys(a.in_process).length });
  }

  if (sortBy === 'Most Candidates Accepted') {
    newArray.sort(function (a, b) { return Object.keys(b.accepted_candidates).length - Object.keys(a.accepted_candidates).length });
  }
  return newArray
}

function JobsOverview() {
  const { state, dispatch } = React.useContext(Context)

  const [index, setIndex] = useState(0)
  const [search, setSearch] = useState()
  const [sortBy, setSortBy] = useState('Most Recent')
  return (
    <div>

      <Breadcrumbs links={[
        {
          name: 'Jobs Overview',
        },
      ]} />

      <div className='columns'>
        <div className='column is-6'>
          <Tabs
            tabs={['Active Jobs (' + Object.keys(state.recruiter.activeJobs).length + ')', 'Archived Jobs (' + Object.keys(state.recruiter.archivedJobs).length + ')']}
            position={index}
            setPosition={setIndex}
          />
        </div>
        <div className='column is-6' style={{ textAlign: 'right' }}>
          <Link to='/jobs/new' className='no-underline'>
            <PrimaryButton
              label='Post New Job'
            />
          </Link>

        </div>
      </div>
      <div className='columns'>
        <div className='column'>
          <input
            className="input"
            type="text"
            placeholder="Search your jobs"
            onChange={event => setSearch(event.target.value)}
            value={search}
          />
        </div>
        <div className='column' style={{ textAlign: 'right' }}>
          <span style={{ marginRight: 12, lineHeight: 2.5 }}>Sort by:</span>
          <span className="select">
            <select value={sortBy} onChange={event => setSortBy(event.target.value)}>
              {['Most Recent', 'Most New Matches', 'Most In Process', 'Most Candidates Accepted'].map((sortByType, i) => (
                <option key={'sortby-type-' + i}>{sortByType}</option>
              ))}
            </select>
          </span>
        </div>
      </div>
      {index === 0 && state.recruiter.activeJobs && searchArray(state.recruiter.activeJobs, search, sortBy).map(job => (
        <JobOverview
          key={'job-overview-' + job.id}
          position={job.position}
          job_id={job.id}
          location={job.location}
          new_messages={job.new_messages}
          dispatch={dispatch}
          controls={[
            <span style={{ marginRight: 48 }} key={'edit-button-' + job.id}>
              <Link to={'/edit'}>
                <EditButtonV2 />
              </Link>
            </span>,
            <span style={{ marginRight: 48 }} key={'share-button-' + job.id}>
              <ShareButtonV2 />
            </span>,
            <span style={{ marginRight: 48 }} key={'archive-button-' + job.id}>
              <ArchiveButton
                archive={() => {
                  dispatch({ type: 'ARCHIVE_JOB', job_id: job.id })
                }}
                name={job.position}
              />
            </span>,
          ]}
        >
          <div style={{ paddingBottom: 8 }}>
            <Link to={RouteJobCandidatesNewMatches(job.id)}>
              <InlineIconLabel icon='fas fa-rocket'>
                <strong>{Object.keys(job.new_matches).length}</strong> New Matches
                </InlineIconLabel>
            </Link>
          </div>
          <div style={{ paddingBottom: 8 }}>
            <Link to={RouteJobCandidatesInProcess(job.id)}>
              <InlineIconLabel icon='fas fa-comment-dots'>
                <strong>{Object.keys(job.in_process).length}</strong> In Process
                </InlineIconLabel>
            </Link>
          </div>
          <div>
            <Link to={RouteJobCandidatesCandidatesAccepted(job.id)}>
              <InlineIconLabel icon='fas fa-user-check'>
                <strong>{Object.keys(job.accepted_candidates).length}</strong> Candidates Accepted
                </InlineIconLabel>
            </Link>
          </div>
        </JobOverview>
      ))}

      {index === 0 && Object.keys(state.recruiter.activeJobs).length === 0 && <div style={{ marginTop: 24, textAlign: 'center' }}>
        <h3>No jobs, yet</h3>
        <p>You have yet to create a job posting. But nothing is stopping you. Just smash that `Post New Job` button and I think you'll find, Bob's your uncle.</p>
      </div>}

      {index === 1 && state.recruiter.archivedJobs && searchArray(state.recruiter.archivedJobs, search, sortBy).map(job => (
        <JobOverview
          key={'job-overview-' + job.id}
          position={job.position}
          location={job.location}
          controls={[
            <span style={{ marginRight: 48 }}>
              <DeleteJobButton
                delete={() => {
                  dispatch({ type: 'DELETE_JOB', job_id: job.id })
                }}
                name={job.position}
              />
            </span>,
            <span style={{ marginRight: 48 }}>
              <ShareButtonV2 />
            </span>,
            <span style={{ marginRight: 48 }}>
              <RepostJobButton
                repost={() => {
                  dispatch({ type: 'REPOST_JOB', job_id: job.id })
                }}
                name={job.position}
              />
            </span>,
          ]}
        >
          <div style={{ paddingBottom: 8 }}>
            <Link to={RouteJobCandidatesNewMatches(job.id)}>
              <InlineIconLabel icon='fas fa-rocket'>
                <b>{job.cvs_received}</b> CVs Received
                </InlineIconLabel>
            </Link>
          </div>
          <div style={{ paddingBottom: 8 }}>
            <Link to={RouteJobCandidatesInProcess(job.id)}>
              <InlineIconLabel icon='fas fa-comment-dots'>
                <strong>{job.conversations}</strong> Conversations
                </InlineIconLabel>
            </Link>
          </div>
        </JobOverview>
      ))}

      {index === 1 && Object.keys(state.recruiter.archivedJobs).length === 0 && <div style={{ marginTop: 24, textAlign: 'center' }}>
        <h3>Too soon</h3>
        <p>None of your jobs have expired, visit here in the future to see you expired jobs.</p>
      </div>}

    </div >
  )
}

export default JobsOverview

function JobOverview(params) {
  return (
    <div style={{ borderBottom: '1px solid #E4E4E4', marginBottom: 24 }}>
      <div className='columns is-gapless is-marginless'>
        <div className='column is-6'>
          <span>
            <h2 style={{ display: 'inline', marginRight: 12 }}>{params.position}</h2>
          </span>
          <div style={{ marginTop: 42, paddingBottom: 12 }}>
            {params.controls}
          </div>
        </div>
        <div className='column is-4' style={{ marginTop: 12 }}>
          {(params.new_messages || params.new_messages === 0) &&
            <Link to={'/messaging/' + params.job_id}>
              <span
                className="new-messages-badge"
                style={{
                  backgroundColor: `${params.new_messages > 0 ? '#956EDC' : '#E4E4E4'}`,
                  color: '#FFFFFF',
                  padding: 4,
                  paddingLeft: 8,
                  paddingRight: 8,
                  fontSize: 14,
                }}
              >
                <strong style={{ color: '#FFFFFF' }}>{params.new_messages}</strong> New Message{params.new_messages > 1 ? 's' : ''}
              </span>
            </Link>}

        </div>
        <div className='column is-2'>
          <div className=' job-overview-side-candidates'>
            {params.children}
          </div>
        </div>
      </div>
    </div>
  )
}